              <h4>PLANTA SEPARADORA LOMA LA LATA</h4>
              <p>Ubicada en el corazn del yacimiento de gas natural argentino, Loma La Lata, provincia de Neuqun.</p>
              <h6>Separacin del gas de entrada:</h6>
              <p>El gas que ingresa a la planta es enviado a un separador de alimentacin a fin de eliminar cualquier partcula extraa previo a su envo al sector de deshidratacin.</p>
              <h6>Deshidratacin:</h6>
              <p>Para separar los componentes ricos, la corriente del gas natural que entra a la Planta Separadora se somete a un proceso de enfriamiento. Sin embargo, para poder realizar este proceso, es condicin precedente quitarle al gas el agua que trae consigo. La etapa de deshidratacin deja al gas con un contenido de agua menor a una parte por milln. El sistema de deshidratacin consta de cinco lechos: cuatro operativos y uno de regeneracin.</p>
              <h6>Recuperacin:</h6>
              <p>El gas deshidratado se divide en dos corrientes para ingresar cada una a un tren de separacin criognica. El proceso consiste en una expansin hasta los 26Kg/cm2, disminuyendo la temperatura del gas hasta los -97C. En estas condiciones ingresa a la torre demetanizadora, separando al gas natural en sus diferentes componentes. De esta manera el etano, propano, butano y gasolina natural se condensan y son recogidos en estado lquido por el fondo de la torre (NGL). El metano residual, todava en estado gaseoso, es recuperado por el tope de la torre para su acondicionamiento e inyeccin a gasoductos. El sistema de separacin permite una eficiencia de hasta 88% en la recuperacin de etano y superior a 96% en la recuperacin de propano.</p>
              <h6>Estacin de compresin de gas:</h6>
              <p>El gas residual obtenido de la demetanizacin, mayoritariamente metano, es recomprimido a fin de inyectarlo al gasoducto. Para este fin la planta cuenta con un sistema de compresin de 93000hp que descarga a un cabezal desde donde se alimentan los principales gasoductos troncales del pas.</p>

              <h4>POLIDUCTO</h4>
              <p>El objetivo del poliducto es el de transportar el lquido recuperado (que mayoritariamente es etano, propano, butano y gasolina natural) desde la planta de Loma La Lata hasta la planta de Baha Blanca.</p>

              <h4>PLANTA FRACCIONADORA DE BAHA BLANCA</h4>
              <p>En la planta se recibe el lquido recuperado en Loma La Lata y transportado a travs de 600 km de ducto, y se fracciona el etano, propano, butano y gasolina natural mediante procesos de destilacin (deetanizacin, depropanizacin y debutanizacin).</p>
              <h6>Deetanizacin, Depropanizacin y Debutanizacin:</h6>
              <p>El lquido recuperado en Loma La Lata que ingresa a este sector de destilacin debe ser precalentado para garantizar una temperatura constante de alimentacin a la deetanizadora. Este precalentamiento se lleva a cabo intercambiando calor con gasolina natural, que a su vez es pre-enfriada antes de su almacenamiento. Durante la deetanizacin se separa el etano del lquido formado por propano, butano y gasolina natural, para su posterior tratamiento. Esta mezcla lquida es acondicionada para llevar a cabo la depropanizacin en la que se obtiene el propano y una mezcla lquida constituida por butano y gasolina natural que nuevamente es acondicionada para ingresar a la columna de destilacin donde se realiza la debutanizacin. En esta columna de destilacin se obtienen el butano y la gasolina natural. Tanto el propano como el butano y la gasolina natural son acondicionados trmicamente para su almacenamiento.</p>
              <h6>Tratamiento de etano:</h6>
              <p>El sector de tratamiento de etano consiste en una unidad de remocin de dixido de carbono. La remocin del dixido de carbono se basa en una tecnologa con aminas que deja el etano prcticamente libre de dixido de carbono. El anhdrido carbnico se acondiciona en una etapa posterior para eliminar el cido sulfhdrico y se comercializa parcialmente.</p>
              <h6>Sistema de refrigeracin de productos:</h6>
              <p>El sistema de refrigeracin de productos proporciona el fro requerido para el almacenamiento de propano, butano, gasolina natural y la condensacin de la deetanizacin.</p>
              <h6>Almacenamiento de productos:</h6>
              <p>El sector de almacenamiento est compuesto por tanques de propano, butano y gasolina natural. Todos los productos se almacenan a presin atmosfrica. La gasolina natural se almacena en tanques de techo flotante, mientras que el propano y el butano se almacenan en tanques refrigerados.</p>
         
